//vuex状态管理
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js'
import actions from './actions.js'

//全局注册
Vue.use(Vuex);

//创建Store对象
const store = new Vuex.Store({
	//状态数据
	state:{
		//主题分类数据
		topics:[
  			{name:'all',desc:'全部'/*,selected:true*/},
  			{name:'good',desc:'精华'/*,selected:false*/},
  			{name:'share',desc:'分享'/*,selected:false*/},
  			{name:'ask',desc:'问答'/*,selected:false*/},
  			{name:'job',desc:'招聘'/*,selected:false*/},
  			{name:'dev',desc:'测试'/*,selected:false*/}
  		],
  		//未登录时的菜单
  		unLoginedMoreMenus:{
  			login:'登录'
  		},
  		//登录后的更多菜单
  		loginedMoreMenus:{
  			create:'发表主题',
  			center:'个人中心',
  			msg:'消息通知',
  			logout:'退出'
  		},
  		//登录的状态
  		isShowMenus:false,
  		//登录用户的accesstoken
  		accesstoken:'',
  		//登录用户id
  		author_id:'',
  		//是否显示更多菜单
  		isLogined:false,
  		//登录用户的accesstoken
  		
	},
	//状态数据的计算属性
	getters:{
		showMoreMenus:function(state){
  		if(state.isLogined){
  			//用户处理登录状态，返回登录后的更多菜单
  			return state.loginedMoreMenus;
  			};
  			return state.unLoginedMoreMenus;
  	},
  	getTopicDescByTab:function(state){
  		//需要返回一个函数结构
  		return function(tab){
  			for (var i=0;i<state.topics.length;i++) {
  				if (state.topics[i].name == tab) {
  					return state.topics[i].desc;
  				}
  			}
  		}
  	}
	},
	mutations,
	actions
});

export default store;



